import React from 'react';
import { motion } from 'framer-motion';
import { Download, Calendar } from 'lucide-react';
import { StatusBadge } from './StatusBadge';
import { Project } from '../store/projectStore';

interface ProjectCardProps {
  project: Project;
}

export function ProjectCard({ project }: ProjectCardProps) {
  return (
    <motion.div
      initial={{ opacity: 0, x: -20 }}
      animate={{ opacity: 1, x: 0 }}
      className="bg-white/80 backdrop-blur-sm p-6 rounded-lg shadow-sm hover:shadow-lg transition-all duration-200"
    >
      <div className="flex items-start justify-between">
        <div className="flex-1">
          <div className="flex items-center mb-3">
            <h3 className="text-xl font-semibold text-gray-900 mr-3">
              {project.name}
            </h3>
            <StatusBadge status={project.status} />
          </div>
          {project.taskType && (
            <div className="text-sm text-m6-red-600 mb-2">
              Type: {project.taskType}
            </div>
          )}
          <p className="text-gray-600 mb-3">{project.description}</p>
          <div className="flex items-center text-sm text-gray-500">
            <Calendar className="w-4 h-4 mr-1" />
            Soumis le {project.date}
          </div>
        </div>
        {project.status === 'termine' && project.deliverables && (
          <button className="btn-secondary ml-4">
            <Download className="w-4 h-4 mr-2" />
            Télécharger les livrables
          </button>
        )}
      </div>
    </motion.div>
  );
}