import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { Send } from 'lucide-react';
import { useNavigate } from 'react-router-dom';
import { FileUpload } from '../components/FileUpload';
import { useProjectStore } from '../store/projectStore';

function SubmitProject() {
  const navigate = useNavigate();
  const { addProject } = useProjectStore();
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    taskType: '',
    file: null as File | null
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    addProject({
      name: formData.name,
      description: formData.description,
      taskType: formData.taskType,
      file: formData.file
    });
    navigate('/tracking');
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleFileChange = (file: File) => {
    setFormData({
      ...formData,
      file
    });
  };

  const taskTypes = [
    "Automatisation de processus",
    "Analyse de données",
    "Intégration d'IA",
    "Optimisation de workflow",
    "Autre"
  ];

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      className="max-w-3xl mx-auto px-4 py-12"
    >
      <div className="text-center mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">
          Soumettre un nouveau projet
        </h1>
        <p className="text-gray-600">
          Nous sommes là pour vous aider à automatiser vos processus et à intégrer l'IA dans vos projets.
        </p>
      </div>

      <form onSubmit={handleSubmit} className="bg-white shadow-md rounded-lg p-8">
        <div className="mb-6">
          <label htmlFor="name" className="block text-gray-700 text-sm font-bold mb-2">
            Nom du projet
          </label>
          <input
            type="text"
            id="name"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
            className="input-primary"
            placeholder="Entrez le nom de votre projet"
          />
        </div>

        <div className="mb-6">
          <label htmlFor="taskType" className="block text-gray-700 text-sm font-bold mb-2">
            Type de tâche souhaitée
          </label>
          <select
            id="taskType"
            name="taskType"
            value={formData.taskType}
            onChange={handleChange}
            required
            className="input-primary"
          >
            <option value="">Sélectionnez une tâche</option>
            {taskTypes.map((type) => (
              <option key={type} value={type}>
                {type}
              </option>
            ))}
          </select>
        </div>

        <div className="mb-6">
          <label className="block text-gray-700 text-sm font-bold mb-2">
            Fichier du projet
          </label>
          <FileUpload
            onFileChange={handleFileChange}
            selectedFile={formData.file}
          />
        </div>

        <div className="mb-6">
          <label htmlFor="description" className="block text-gray-700 text-sm font-bold mb-2">
            Description détaillée
          </label>
          <textarea
            id="description"
            name="description"
            value={formData.description}
            onChange={handleChange}
            required
            rows={6}
            className="input-primary"
            placeholder="Décrivez votre projet et vos besoins en détail..."
          />
        </div>

        <div className="flex justify-center">
          <button type="submit" className="btn-primary">
            <Send className="w-5 h-5 mr-2" />
            Soumettre le projet
          </button>
        </div>
      </form>
    </motion.div>
  );
}

export default SubmitProject;