import React, { useRef } from 'react';
import { Upload, FileText } from 'lucide-react';

interface FileUploadProps {
  onFileChange: (file: File) => void;
  selectedFile: File | null;
}

export function FileUpload({ onFileChange, selectedFile }: FileUploadProps) {
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      onFileChange(e.target.files[0]);
    }
  };

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    e.stopPropagation();
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    e.stopPropagation();
    
    if (e.dataTransfer.files && e.dataTransfer.files[0]) {
      onFileChange(e.dataTransfer.files[0]);
    }
  };

  return (
    <div
      className="mt-1 flex justify-center px-6 pt-5 pb-6 border-2 border-gray-300 border-dashed rounded-md hover:border-m6-red-500 transition-colors cursor-pointer"
      onDragOver={handleDragOver}
      onDrop={handleDrop}
      onClick={() => fileInputRef.current?.click()}
    >
      <div className="space-y-1 text-center">
        <Upload className="mx-auto h-12 w-12 text-gray-400" />
        <div className="flex text-sm text-gray-600">
          <label className="relative cursor-pointer rounded-md font-medium text-m6-red-600 hover:text-m6-red-500">
            <span>Sélectionnez un fichier</span>
            <input
              type="file"
              ref={fileInputRef}
              onChange={handleFileChange}
              className="sr-only"
            />
          </label>
          <p className="pl-1">ou glissez-déposez</p>
        </div>
        <p className="text-xs text-gray-500">
          Tous types de fichiers jusqu'à 10MB
        </p>
        {selectedFile && (
          <div className="flex items-center justify-center text-sm text-gray-600 mt-2">
            <FileText className="w-4 h-4 mr-2" />
            {selectedFile.name}
          </div>
        )}
      </div>
    </div>
  );
}