import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { SearchBar } from '../components/SearchBar';
import { ProjectCard } from '../components/ProjectCard';
import { useProjectStore } from '../store/projectStore';

function ProjectTracking() {
  const [searchTerm, setSearchTerm] = useState('');
  const { projects, searchProjects } = useProjectStore();
  const [filteredProjects, setFilteredProjects] = useState(projects);

  useEffect(() => {
    setFilteredProjects(searchProjects(searchTerm));
  }, [searchTerm, projects]);

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      className="max-w-7xl mx-auto px-4 py-12"
    >
      <div className="text-center mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">Suivi des Projets</h1>
        <p className="text-gray-600">Retrouvez ici l'état d'avancement de vos projets déposés.</p>
      </div>

      <div className="mb-8">
        <SearchBar
          value={searchTerm}
          onChange={setSearchTerm}
          placeholder="Rechercher un projet par nom, description ou type..."
        />
      </div>

      <motion.div 
        className="grid gap-6"
        variants={{
          hidden: { opacity: 0 },
          show: {
            opacity: 1,
            transition: {
              staggerChildren: 0.1
            }
          }
        }}
        initial="hidden"
        animate="show"
      >
        {filteredProjects.map((project) => (
          <ProjectCard key={project.id} project={project} />
        ))}
        {filteredProjects.length === 0 && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="text-center py-12 bg-white/80 backdrop-blur-sm rounded-lg shadow-sm"
          >
            <p className="text-gray-500 text-lg">
              Aucun projet ne correspond à votre recherche
            </p>
            <p className="text-gray-400 text-sm mt-2">
              Essayez de modifier vos critères de recherche
            </p>
          </motion.div>
        )}
      </motion.div>
    </motion.div>
  );
}

export default ProjectTracking;