import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { Edit2, Upload, Save } from 'lucide-react';
import { useProjectStore } from '../store/projectStore';
import { StatusBadge } from '../components/StatusBadge';

function Admin() {
  const { projects, updateProjectStatus } = useProjectStore();
  const [editingId, setEditingId] = useState<number | null>(null);

  const handleStatusChange = (id: number, newStatus: 'transmis' | 'en_cours' | 'termine') => {
    updateProjectStatus(id, newStatus);
  };

  const handleSave = (id: number) => {
    setEditingId(null);
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      className="max-w-7xl mx-auto px-4 py-12"
    >
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-3xl font-bold text-gray-900">Administration des projets</h1>
      </div>

      <div className="bg-white shadow-md rounded-lg overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Projet
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Status
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Date
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Actions
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {projects.map((project) => (
              <tr key={project.id} className="hover:bg-gray-50">
                <td className="px-6 py-4">
                  <div className="text-sm font-medium text-gray-900">{project.name}</div>
                  <div className="text-sm text-gray-500">{project.description}</div>
                  {project.taskType && (
                    <div className="text-xs text-gray-400 mt-1">{project.taskType}</div>
                  )}
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  {editingId === project.id ? (
                    <select
                      className="mt-1 block w-full pl-3 pr-10 py-2 text-base border-gray-300 focus:outline-none focus:ring-red-500 focus:border-red-500 sm:text-sm rounded-md"
                      value={project.status}
                      onChange={(e) => handleStatusChange(project.id, e.target.value as 'transmis' | 'en_cours' | 'termine')}
                    >
                      <option value="transmis">Transmis</option>
                      <option value="en_cours">En cours</option>
                      <option value="termine">Terminé</option>
                    </select>
                  ) : (
                    <StatusBadge status={project.status} />
                  )}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {project.date}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                  {editingId === project.id ? (
                    <button
                      onClick={() => handleSave(project.id)}
                      className="text-red-600 hover:text-red-900 mr-4"
                    >
                      <Save className="w-5 h-5" />
                    </button>
                  ) : (
                    <button
                      onClick={() => setEditingId(project.id)}
                      className="text-red-600 hover:text-red-900 mr-4"
                    >
                      <Edit2 className="w-5 h-5" />
                    </button>
                  )}
                  {project.status === 'termine' && (
                    <button className="text-gray-600 hover:text-gray-900">
                      <Upload className="w-5 h-5" />
                    </button>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </motion.div>
  );
}

export default Admin;