import { create } from 'zustand';
import { persist, createJSONStorage } from 'zustand/middleware';

export interface Project {
  id: number;
  name: string;
  status: 'transmis' | 'en_cours' | 'termine';
  date: string;
  description: string;
  taskType?: string;
  file?: File | null;
  deliverables?: string;
}

interface ProjectState {
  projects: Project[];
  addProject: (project: Omit<Project, 'id' | 'status' | 'date'>) => void;
  updateProjectStatus: (id: number, status: Project['status']) => void;
  searchProjects: (term: string) => Project[];
}

export const useProjectStore = create<ProjectState>()(
  persist(
    (set, get) => ({
      projects: [
        {
          id: 1,
          name: "Automatisation des rapports",
          status: "transmis",
          date: "2024-03-15",
          description: "Automatisation de la génération des rapports mensuels",
          taskType: "Automatisation de processus"
        },
        {
          id: 2,
          name: "Chatbot Service Client",
          status: "en_cours",
          date: "2024-03-10",
          description: "Implementation d'un chatbot pour le service client",
          taskType: "Intégration d'IA"
        },
        {
          id: 3,
          name: "Analyse des données viewers",
          status: "termine",
          date: "2024-03-01",
          description: "Analyse des comportements des téléspectateurs",
          taskType: "Analyse de données",
          deliverables: "rapport_final.pdf"
        }
      ],
      
      addProject: (projectData) => {
        const newProject: Project = {
          ...projectData,
          id: Date.now(),
          status: 'transmis',
          date: new Date().toISOString().split('T')[0]
        };
        set((state) => ({
          projects: [newProject, ...state.projects] // Ajoute le nouveau projet au début du tableau
        }));
      },

      updateProjectStatus: (id, status) => {
        set((state) => ({
          projects: state.projects.map(project =>
            project.id === id ? { ...project, status } : project
          )
        }));
      },

      searchProjects: (term) => {
        const { projects } = get();
        const searchTerm = term.toLowerCase().trim();
        
        if (!searchTerm) {
          // Retourne les projets triés par date décroissante
          return [...projects].sort((a, b) => 
            new Date(b.date).getTime() - new Date(a.date).getTime()
          );
        }
        
        return projects
          .filter((project) =>
            project.name.toLowerCase().includes(searchTerm) ||
            project.description.toLowerCase().includes(searchTerm) ||
            project.taskType?.toLowerCase().includes(searchTerm)
          )
          .sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
      }
    }),
    {
      name: 'm6-projects-storage',
      storage: createJSONStorage(() => localStorage),
      partialize: (state) => ({
        projects: state.projects.map(project => ({
          ...project,
          file: null
        }))
      })
    }
  )
);